<template>
    <div class="register">
        <van-nav-bar
        title="登录"
        left-text="注册"
        right-text="游客"
        @click-left="goPage('Register')"
        @click-right="goPage('Home')"
        />
        <div class="logo">
            <img class="auto-img" src="../assets/images/logo1.png">
            <h2>元気·咖啡</h2>
            <h5>一杯元気，给你活力满满的一天！</h5>
        </div>

        <van-form @submit="login">
            <van-field
                v-model="userInfo.phone"
                type="text"
                name="手机号"
                label="手机号"
                placeholder="输入11位手机号"
                left-icon="phone-o"
                maxlength='11'
            />
            <van-field
                v-model="userInfo.password"
                :type="isEye ? 'text' : 'password' "
                name="密码"
                label="密码"
                placeholder="字母开头,字母数字_ (6-16位)"
                left-icon="bag-o"
                :right-icon="isEye ? 'eye-o' : 'closed-eye' "
                @click-right-icon="viewPassword"
                maxlength='16'
            />

            <div class="register-btn">
                <van-button class="btn"  round block type="info" native-type="submit">立即登录</van-button>
                <div class="forgot-box">
                    <div class="fr forgotpwd" @click="forgot">忘记密码</div>
                </div>
            </div>
           

           
        </van-form>
    </div>
</template>

<script>
    // 导入样式
    import '../assets/css/register.less'

    // 导入验证表单模块：assets/js/validForm.js
    import validForm from '../assets/js/validForm'


    export default {
        data(){
            return{
                // 用户注册信息
                userInfo:{
                    phone:'',
                    password:'',
                    
                },
                // 密码的小眼睛初始是关闭的
                isEye:false

            }
        },
        methods:{
             // 跳转注册页面
            goPage(name){
                this.$router.push({name})
            },
            // 点击小眼睛睁开和关闭
            viewPassword(){
                this.isEye = !this.isEye;
            },

            // 登录
            login(){
                // 进行验证表单
                if(validForm.validUserForm(this.userInfo)){

                    //合并对象(复制对象)
                   let data = Object.assign({},this.userInfo);
                     //请求密钥
                   data.appkey = this.appkey;

                    this.$toast.loading({
                        message: '加载中...',
                         //forbidClick 属性可以禁用背景点击
                        forbidClick: true,
                        // 延迟自动关闭加载提示，如果值为0，不会自动关闭,需要下面设置关闭
                        duration:0
                    });

                   //发起注册请求
                   this.axios({
                       method: 'POST',
                      //接口路径，（拼接main.js中请求基础路径，才是完整接口路径）
                       url:"/login",

                       //post传递参数需要写在data，
                       data

                       
                   }).then(result => {
                    //    console.log(result );
                        // 关闭加载提示
                        this.$toast.clear();
                         if(result.data.code == 200){
                            // 跳转到菜单页面
                            // console.log('登录成功');
                            
                            this.$router.push({name: 'Home'});
                            
                            //存储token,用于后面登录验证
                            localStorage.setItem('__Tk',result.data.token);

                        }else{
                             this.$toast(result.data.msg);
                        }
                   }).catch(err =>{
                    //    console.log(err);
                        // 关闭加载提示
                        this.$toast.clear();
                   })
                }
            },

            //找回密码
            forgot() {
                this.$router.push({name: 'Forgot'});
            }
        }
    }
</script>

<style lang="less" scoped>
   .fotgot-box{
       margin-top: 20px;
   }
   h5{
       color: #ddd;
   }
</style>